"use client";

import Image from "next/image";
import { use } from "react"

interface AccountData {
  avatar: string;
  name: string;
  visibility: number;
  accountAge: number;
}

interface SearchResult {
  accountData: AccountData;
  accountVault: string;
}


export default function ProfilePage({ searchParams } : { searchParams: { [key: string]: string }}) {
  const result: SearchResult | null = use(
    (async () => {
      if (!searchParams.data) return null;
      return JSON.parse(decodeURIComponent(searchParams.data));
    })()
  );

  
  if (!result) {
    return <h1>No data found or invalid search parameters.</h1>;
  }

  return (
    <div>
      <Image src={result.accountData.avatar} alt="Avatar" width={50} height={50}/>
    </div>
  )
}
